.navbar{
    display:flex;
    justify-content:space-between;
    padding: 10px ;
    box-shadow: 0 1px 3px -2px rgb(171, 59, 59);

}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo{
    color: #ac2626;
    font-size: 1px;
    font-weight: 100;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #ac2626;
    font-size: 15px;
    font-weight: 500px;
}
.nav-menu li{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
gap:3px;
cursor: pointer;

}
.nav-menu hr{
    border: none;
    width: 80px;
    height:3px;
    border-radius: 10px;
    background: #ac2626;

}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;

}
.nav-login-cart button{
    width: 157px;
    height: 58;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 15px;
    font-weight: 500;
    background: white;
    cursor: pointer;


}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: red;
    color: white;
}